import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ContratoIndexComponent } from './contrato-index/contrato-index.component';
import { ContratoAddComponent } from './contrato-add/contrato-add.component';
import { ContratoEditComponent } from './contrato-edit/contrato-edit.component';
import { Candeactivateguard } from '../shared/can-deactivate-guard/can-deactivate-guard.service';

const routes: Routes = [{ path: '', component: ContratoIndexComponent, canDeactivate: [Candeactivateguard] },
{ path: 'add', component: ContratoAddComponent, canDeactivate: [Candeactivateguard] },
{ path: 'edit/:id', component: ContratoEditComponent, canDeactivate: [Candeactivateguard] },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ContratosRoutingModule { }



