import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { CargararchivoComponent } from './cargararchivo/cargararchivo/cargararchivo.component';
import { FileUploadModule } from 'ng2-file-upload';
import { MostrararchivoComponent } from './mostrararchivo/mostrararchivo/mostrararchivo.component';

@NgModule({
  imports: [
    CommonModule,
    FileUploadModule,
  ],
  declarations: [CargararchivoComponent, MostrararchivoComponent],
  exports: [CargararchivoComponent,MostrararchivoComponent]
})
export class ComponentesModule { }



