

import { Injectable } from '@angular/core';
import { Observable } from "rxjs";
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import { EnvironmentService } from './../shared/environment/environment.service';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { environment } from '../../environments/environment.prod';
import { Observer } from 'rxjs/Observer';

@Injectable()
export class MapsService {
    geocoder: google.maps.Geocoder;
    service: google.maps.DistanceMatrixService;
    constructor(private _http: HttpClient, ) {

        this.geocoder = new google.maps.Geocoder();
        this.service= new google.maps.DistanceMatrixService();
    }

    public getlocationbyaddress(address) {
        let url = 'https://maps.googleapis.com/maps/api/geocode/xml?address=' + address + '&key=' + environment.apimap;
        return this._http.post(url, null)
            .map(res => res)
            .catch(this.handleError)
    }

    public getdistance(latorig, lngorigin, latdest, lngdest) {
        let origin= new google.maps.LatLng(latorig, lngorigin);
        let dest= new google.maps.LatLng(latdest, lngdest);
        return Observable.create((observer: Observer<google.maps.DistanceMatrixResponse>) => {
            // Invokes geocode method of Google Maps API geocoding.
            this.service.getDistanceMatrix({ 
                origins:[origin],
                destinations:[dest],
                travelMode:google.maps.TravelMode.DRIVING ,
                unitSystem:google.maps.UnitSystem.METRIC
            }, (
                (response: google.maps.DistanceMatrixResponse, status: google.maps.DistanceMatrixStatus) => {
                    if (status === google.maps.DistanceMatrixStatus.OK) {
                        observer.next(response);
                        observer.complete();
                    } else {
                        console.log(
                            'Geocoding service: geocode was not successful for the following reason: '
                            + status
                        );
                        observer.error(status);
                    }
                })
            );
        });
    }
    codeAddress(address: string): Observable<google.maps.GeocoderResult[]> {
        return Observable.create((observer: Observer<google.maps.GeocoderResult[]>) => {
            // Invokes geocode method of Google Maps API geocoding.
            this.geocoder.geocode({ address: address }, (
                (results: google.maps.GeocoderResult[], status: google.maps.GeocoderStatus) => {
                    if (status === google.maps.GeocoderStatus.OK) {
                        observer.next(results);
                        observer.complete();
                    } else {
                        console.log(
                            'Geocoding service: geocode was not successful for the following reason: '
                            + status
                        );
                        observer.error(status);
                    }
                })
            );
        });
    }

    private handleError(error: HttpErrorResponse | any) {
        let errMsg: string;
        errMsg = error.error
        console.error(errMsg);
        return Observable.throw(errMsg);

    }

}



