import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
/*modules */
import {FileUploadModule} from 'ng2-file-upload';
import { AuthenticationModule } from './../shared';
import { EnvironmentModule } from './../shared';
import { HttpModule } from '@angular/http';
import { FormValidationModule } from './../shared';
import { NotificationModule } from './../shared';
import { LocalDataModule } from './../shared';
import { AlertModule } from 'ngx-bootstrap/alert';
import { SweetAlert2Module } from '@toverux/ngx-sweetalert2';
import { SnotifyModule, SnotifyService, ToastDefaults } from 'ng-snotify';
import { TypeaheadModule } from 'ngx-bootstrap/typeahead';
import { BsDatepickerModule } from 'ngx-bootstrap/datepicker';
import { ContratosRoutingModule } from './contratos-routing.module';
import { ContratoAddComponent } from './contrato-add/contrato-add.component';
import { ContratoEditComponent } from './contrato-edit/contrato-edit.component';
import { ContratoIndexComponent } from './contrato-index/contrato-index.component';
import { RequestInterceptor } from '../shared/interceptor/http_interceptor';
import { ContratoService } from './contrato.service';
import { defineLocale } from 'ngx-bootstrap/chronos';
import { esLocale } from 'ngx-bootstrap/locale';
import { CKEditorModule } from 'ng2-ckeditor';
import { DiasWeekModule} from './../dias-week/dias-week.module';
import { ModalModule } from 'ngx-bootstrap/modal';
import { Candeactivateguard } from '../shared/can-deactivate-guard/can-deactivate-guard.service';

defineLocale('es', esLocale);
@NgModule({
  imports: [
    CommonModule,
    ContratosRoutingModule,
    FormsModule,
    ReactiveFormsModule,

    AlertModule.forRoot(),
    SweetAlert2Module.forRoot(),
    SnotifyModule,
    HttpModule,
    AuthenticationModule,
    EnvironmentModule,
    FormValidationModule,
    NotificationModule,
    LocalDataModule,
    TypeaheadModule.forRoot(),
    BsDatepickerModule.forRoot(),
    CKEditorModule,
    DiasWeekModule,
    ModalModule.forRoot(),
    FileUploadModule,
  ],
  declarations: [ContratoAddComponent, ContratoEditComponent, ContratoIndexComponent,],
  providers: [ContratoService,Candeactivateguard,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: RequestInterceptor,
      multi: true,
    }],
    exports:[ContratoAddComponent]
})
export class ContratosModule { }



