import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ContratoService } from "./../contrato.service";
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { NotificationService } from './../../shared';
import { LocalDataService } from './../../shared';
import { tipocomponente } from '../../viewmodel/tipocomponente';
import { Contrato } from '../../viewmodel/contrato';
import { Observable } from 'rxjs';
import { ClienteService } from '../../clientes/cliente.service';
import { TypeaheadMatch } from 'ngx-bootstrap/typeahead';
import { listLocales } from 'ngx-bootstrap/chronos';
import { BsDatepickerConfig, BsLocaleService } from 'ngx-bootstrap/datepicker';
import { mergeMap } from 'rxjs/operators';
import { TipocontratoService } from '../../tipocontrato/tipocontrato.service';
import * as moment from 'moment';
import { estacionservicio } from '../../viewmodel/estacionservicio';
import { EstacionservicioService } from '../../estacionservicio/estacionservicio.service';
import { SwalComponent } from '@toverux/ngx-sweetalert2';
import { Componente } from '../../viewmodel/Componente';
import { ComponenteActuacion } from '../../viewmodel/componente_actuacion';
import { tipocontrato } from '../../viewmodel/tipocontrato';
import { Candeactivateguard } from '../../shared/can-deactivate-guard/can-deactivate-guard.service';
import { FileUploader, FileLikeObject, FileItem, ParsedResponseHeaders } from 'ng2-file-upload';
import { environment } from '../../../environments/environment';
import { ArchivouploadService } from '../../archivoupload/archivoupload.service';
declare var $;

@Component({
  selector: 'app-contrato-edit',
  templateUrl: './contrato-edit.component.html',
  styleUrls: ['./contrato-edit.component.css']
})
export class ContratoEditComponent implements OnInit {
  public uploader: FileUploader = new FileUploader({
    url: environment.apiUrl + 'api/archivoincidencia',
    authTokenHeader: "Authorization",
    authToken: "Bearer " + localStorage.getItem('access_token'),
    isHTML5: true,
    autoUpload: true,
    maxFileSize: 5 * 1024 * 1024,

  });
  item: Contrato;
  asyncSelected: string;
  typeaheadLoading: boolean;
  typeaheadNoResults: boolean;
  dataSource: Observable<any>;
  tipocontrato: any;
  name = 'ng2-ckeditor';
  ckeConfig: any;
  mycontent: string;
  newcompo: estacionservicio;
  estacionservicio: any;
  componentesel: Componente;
  actuacionesNew: ComponenteActuacion;
  tipocontratosel: tipocontrato;
  filter: any;
  apiurl: any;
  @ViewChild('createSwal') private createSwal: SwalComponent;
  public hasBaseDropZoneOver: boolean = false;
  public hasAnotherDropZoneOver: boolean = false;
  constructor(private spinnerService: Ng4LoadingSpinnerService,
    private contratoService: ContratoService,
    private router: Router,
    private route: ActivatedRoute,
    private notificationService: NotificationService,
    private clienteService: ClienteService,
    private localeService: BsLocaleService,
    private tipocontratoService: TipocontratoService,
    private estacionservicioService: EstacionservicioService,
    private localDataService: LocalDataService,
    private candeactivateguard: Candeactivateguard,
    private archivouploadService: ArchivouploadService,
  ) {
    this.candeactivateguard.setedit(true);
    this.localeService.use('es');
    this.dataSource = Observable.create((observer: any) => {
      // Runs on every search
      this.clienteService.gettoken(this.item.razonsocial).subscribe((value) => {
        observer.next(value.data);

      });
    });
    this.uploader.onAfterAddingFile = (file: any) => {
      this.uploader.options.additionalParameter = { contrato_id: 0, tipofotosiniestro: 0, tipo: 2 };
      file.withCredentials = false;
    };
    this.uploader.onErrorItem = (item, response, status, headers) => this.onErrorItem(item, response, status, headers);
    this.uploader.onSuccessItem = (item, response, status, headers) => this.onSuccessItem(item, response, status, headers);
    this.uploader.onWhenAddingFileFailed = (fileItem) => {
      this.notificationService.onError('La imagen no se pudo cargar, '
        + 'el tamaño maximo es de 5MB y el formato es png o jpg, INtentelo nuevamente');
    };
    this.apiurl = environment.apiUrl;
  }

  onselectEstacionServicio(estacionserviciosel) {
    let result = this.estacionservicio.filter(comp => comp.id.toString() == estacionserviciosel.target.value);


    if (result.length > 0) {
      this.newcompo = result[0];
    }

  }
  onselecttipocontrato(tipocontratovalue) {

    let result;
    result = this.tipocontrato.filter(comp => comp.id.toString() == tipocontratovalue.target.value);


    if (result.length > 0) {
      this.tipocontratosel = result[0];
    }
  }

  ngOnInit() {
    this.ckeConfig = {
      allowedContent: false,
      extraPlugins: 'divarea',
      forcePasteAsPlainText: true
    };
    this.item = new Contrato;
    this.item.cliente_id = 0;
    this.item.tipocontrato_id = '-1';
    this.item.estacion_servicio_id = '-1';
    this.item.archivos = [];
    this.newcompo = new estacionservicio();
    this.cargarTipoCOntrato();
    this.getById();
    this.actuacionesNew = new ComponenteActuacion();
    this.actuacionesNew.tipo = '-1';
    this.actuacionesNew.periodicidad = '-1';
    this.actuacionesNew.mes = 1;
    this.getparams();
  }
  onChange($event: any): void {
    console.log("onChange");
    //this.log += new Date() + "<br />";
  }
  onValueChange(value: Date): void {

  }
  cargarTipoCOntrato() {

    this.tipocontratoService.get(1).subscribe((value) => {
      this.tipocontrato = value.data;

    });

  }
  cargarEstacionServicio() {
    let empresa = 0;
    this.spinnerService.show();
    empresa = this.localDataService.getLocalData('empresaSeleccionada').id;
    this.estacionservicioService.getbycliente(empresa, this.item.cliente_id).subscribe((value) => {
      this.estacionservicio = value.data;
      this.spinnerService.hide();
    });

  }
  getparams() {
    this.route.queryParamMap.subscribe(params => {
      this.filter = params.get('filter');
    });
  }

  getById() {
    this.spinnerService.show();
    const $id = this.route.snapshot.paramMap.get('id');
    this.contratoService.getById($id)
      .subscribe((response) => {
        this.item = response.data;
        this.item.fechainicio = moment(this.item.fechainicio).toDate();
        this.item.fechafin = moment(this.item.fechafin).toDate();
        this.newcompo = this.item.estacionservicio;
        this.tipocontratosel = this.item.tipocontrato;
        console.log(this.item);
        this.spinnerService.hide();
        this.cargarEstacionServicio();
      });
  }
  update() {
    this.spinnerService.show();
    this.candeactivateguard.setedit(false);
    this.contratoService.update(this.item)
      .subscribe((value) => {
        this.spinnerService.hide();
        this.notificationService.onSuccess('Contrato Actualizado con Exito.');
        if (this.filter == null) {
          this.router.navigate(['contrato']);
        }
        else { this.router.navigate(['estacionservicio/edit/' + this.filter]); }

      });
  }
  changeTypeaheadLoading(e: boolean): void {

    this.typeaheadLoading = e;
  }

  typeaheadOnSelect(e: TypeaheadMatch): void {
    this.item.cliente_id = e.item.codigo_cliente;
    this.cargarEstacionServicio();
  }
  selectComponent(comp) {
    if (comp.select) {
      comp.select = false;
    } else {
      comp.select = true;
    }
    return false;
  }
  modalactuacion(item) {
    this.componentesel = item;
    if (!this.componentesel.actuaciones) {
      this.componentesel.actuaciones = [];
    }
    this.createSwal.show();
    return false;
  }
  agregaractuacion() {
    this.componentesel.actuaciones.push(this.actuacionesNew);
    this.actuacionesNew = new ComponenteActuacion();
    this.actuacionesNew.tipo = '-1';
    this.actuacionesNew.periodicidad = '-1';
    this.actuacionesNew.mes = 1;
    return false;
  }
  deleteactuacion(componente) {
    let index = this.componentesel.actuaciones.indexOf(componente);
    this.componentesel.actuaciones.splice(index, 1);
    return false;
  }

  onvaluechanged(value) {
    if (value.index === undefined || value.index === '') {
      this.actuacionesNew.dias = value.dias;
    } else {
      this.componentesel.actuaciones[value.index].dias = value.dias;
    }

  }
  onErrorItem(item: FileItem, response: string, status: number, headers: ParsedResponseHeaders): any {
    let error = JSON.parse(response); //error server response
    console.log(error);
  }

  public fileOverBase(e: any): void {

    this.uploader.options.additionalParameter = { contrato_id: 0, tipofotosiniestro: 0, tipo: 2 };
    this.hasBaseDropZoneOver = e;
  }

  public fileOverAnother(e: any): void {
    this.uploader.options.additionalParameter = { contrato_id: 0, tipofotosiniestro: 0, tipo: 2 };
    this.hasAnotherDropZoneOver = e;
  }
  onSuccessItem(item: FileItem, response: string, status: number, headers: ParsedResponseHeaders): any {
    if (this.item.archivos == undefined || this.item.archivos == null) {
      this.item.archivos = [];
    }
    this.item.archivos.push($.parseJSON(response).data);
  }
  eliminar_imagen(imagen) {
    this.archivouploadService.delete(imagen.id, 2).subscribe((value) => {
      let indice = this.item.archivos.indexOf(imagen);
      this.item.archivos.splice(indice, 1);
      this.notificationService.onSuccess('Imagen eliminada con exito.');
    });
  }


}



